.mv-banner{
  background-image: url(../assets/images/mv-banner.jpg);
  background-size: cover;
  background-position: center;
  height: 612px;

  h1{
    position: absolute;
    line-height: 1.3;
    width: 592px;
    margin-top: 280px;
  }

  @media (max-width: 780px){
    height: 350px;

    h1{
      margin-top: 200px;
      width: 100%;
    }
  }
}